$calendar-cell-width: 100px;
$calendar-cell-height: 100px;
$calendar-padding: 10px;
$cell-gap: 1px;

$calendar-width:
  $calendar-cell-width * 7 +
  $calendar-padding * 2 +
  $cell-gap * 6;

$cell-border: 1px solid #000;
$cell-bg-color: #eee;
$cell-text-color: #000;
$cell-font-family: arial, helvetica, sans-serif;
$cell-font-size: 30px;

$days:
  "mon" 1,
  "tue" 2,
  "wed" 3,
  "thu" 4,
  "fri" 5,
  "sat" 6,
  "sun" 7;

$cell-hover-bg: pink;
